import articlebrief from '../../components/articlebrief';
import mainpagetop from '../../components/mainpagetop.vue';
import mainpageleft from '@/components/mainpageleft.vue';

import { mapState } from 'vuex';

var loadArticleFlag = false; // 为加载文章加锁
var timer = null; // 为头部做定时器收起
export default {
	data() {
		return {
			title: 'Hello',
			hottitlelist: ['热门标题111', '热门标题222', '热门标题333'],
			showlist: [],
			topArticles: '',
			topHeight: 160,

			userInfo: '',
			totalPage: 1,
			currentPage: 1,
			scrollTop: 0,
			old: {
				scrollTop: 0
			}
		};
	},
	components: {
		articlebrief,
		mainpagetop,
		mainpageleft
	},

	onLoad() {
		var userInfo = this.getGlobalUserInfo();
		if (this.isNull(userInfo)) {
			uni.redirectTo({
				url: '../signin/signin'
			});
			return;
		} else {
			this.userInfo = userInfo; // 刷去默认值(若有)
		}

		this.mySocket.init(); // 初始化 Socket, 离线调试请注释掉

		var page = this.currentPage;
		this.showArticles(page); // 显示文章流

		uni.$on('flash', () => {
			// from submit
			this.refreshArticle();
		});
		// [测试代码块]
	},

	onUnload() {
		// 移除监听刷新事件
		uni.$off('flash');
	},

	onShow() {
		 this.setTabBarIndex(0) //index为当前tab的索引
		
		var userInfo = this.getGlobalUserInfo(); // 查看用户是否登录
		if (!this.isNull(userInfo)) {
			// 设置 userInfo 传给 mainpagetop 组件
			// 更新用户信息缓存... 查询用户信息，并分割邮箱更新到缓存
			this.queryUserInfo(userInfo.id);
		}

		this.getTop3Articles(); // 获取热度榜
		
		this.getScreenSize();
	},

	// onPullDownRefresh() {
	// 	console.log("监听到下拉动作")
	// },

	methods: {
		showArticles: function(page) {
			if (loadArticleFlag) {
				return;
			}
			loadArticleFlag = true;

			uni.showLoading({
				title: '加载中...'
			});
			setTimeout(() => {
				if (loadArticleFlag) {
					loadArticleFlag = false; // 解锁
					uni.hideLoading();
					uni.showToast({
						title: '网络未知错误',
						icon: 'none',
						duration: 1000
					});
				}
			}, 5000); // 延时5s timeout

			var that = this;
			uni.request({
				url: that.$serverUrl + '/article/queryAllArticles',
				method: 'POST',
				data: {
					page: page,
					// pageSize: '',
					userId: that.userInfo.id
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: res => {
					setTimeout(() => {
						// 延时加载
						uni.hideLoading();
						loadArticleFlag = false;

						console.log(res);
						// 判断当前页是不是第一页，如果是第一页，那么设置showList为空
						if (page == 1) {
							that.showlist = [];
						}
						var newArticleList = res.data.data.rows;
						var oldArticleList = that.showlist;
						that.showlist = oldArticleList.concat(newArticleList);
						that.currentPage = page;
						that.totalPage = res.data.data.total;
					}, 300);
				},
				fail: res => {
					uni.hideLoading();
					loadArticleFlag = false;

					console.log('index unirequest fail');
					console.log(res);
				}
			});
		},

		loadMore: function() {
			var that = this;
			var currentPage = that.currentPage;
			console.log(currentPage);
			var totalPage = that.totalPage;
			console.log(totalPage);
			// 判断当前页数和总页数是否相等
			if (currentPage == totalPage) {
				// that.showArticles(1);
				uni.showToast({
					title: '没有更多文章了',
					icon: 'none',
					duration: 1000
				});
			} else {
				var page = currentPage + 1;
				that.showArticles(page);
			}
		},
		refreshArticle: function() {
			uni.showNavigationBarLoading();
			this.showArticles(1);
			uni.hideNavigationBarLoading();
		},
		getTop3Articles() {
			var that = this;
			uni.request({
				url: that.$serverUrl + '/article/getHotTop3',
				method: 'POST',
				data: {
					userId: that.userInfo.id
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: res => {
					that.topArticles = res.data.data;
				}
			});
		},

		/**
		 * 查询用户信息，并分割邮箱更新到缓存
		 */
		queryUserInfo(userId) {
			var that = this;
			uni.request({
				url: that.$serverUrl + '/user/queryUser',
				method: 'POST',
				data: {
					userId: userId
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: res => {
					console.log(res);
					if (res.data.status == 200) {
						var user = res.data.data;
						var finalUser = this.myUser(user); // 分割邮箱地址, 重构 user
						this.setGlobalUserInfo(finalUser); // 把用户信息写入缓存
						this.userInfo = finalUser; // 更新页面用户数据
						// console.log(this.userInfo);
					}
				}
			});
		},

		//动态调整顶部高度
	// 	linkageWithTop: async function(e) {
	// 		var y = e.detail.scrollTop; //获取 scrollTop
	// 		// console.log( y + "scrollTop" )
